
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
  height: 100%;
}
body {
  margin: 0;
  line-height: 1;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  background: #F4F7F8;
  min-height: 100%;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: inherit;
}
button {
  border: 0;
  margin: 0;
  padding: 0;
  text-align: inherit;
  text-transform: inherit;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer;
  overflow: visible;
}
::-moz-focus-inner {
  border: 0;
  padding: 0;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100%;
  height: 100vh;
  width: 100%;
}
.nav__wrap {
  display: none;
}
@media screen and (min-width: 769px) {
.nav__wrap {
      display: inherit;
}
}
.main {
  box-sizing: border-box;
  text-align: center;
}
@media screen and (min-width: 769px) {
.main {
      padding-top: 50px;
      padding-right: 17px;
      padding-left: 17px;
}
}
@media screen and (min-width: 1024px) {
.main {
      padding-left: 34px;
}
}
@font-face {
  font-family: 'Montserrat';
  src: local("Montserrat-Regular"), url("./assets/fonts/subset-Montserrat-Regular.woff2") format("woff2"), url("./assets/fonts/subset-Montserrat-Regular.woff") format("woff"), url("./assets/fonts/subset-Montserrat-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: local("Montserrat-Bold"), url("./assets/fonts/subset-Montserrat-Bold.woff2") format("woff2"), url("./assets/fonts/subset-Montserrat-Bold.woff") format("woff"), url("./assets/fonts/subset-Montserrat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: local("Montserrat Light"), local("Montserrat-Light"), url("./assets/fonts/subset-Montserrat-Light.woff2") format("woff2"), url("./assets/fonts/subset-Montserrat-Light.woff") format("woff"), url("./assets/fonts/subset-Montserrat-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat Semi';
  src: local("Montserrat Semi Bold"), local("Montserrat-SemiBold"), url("./assets/fonts/subset-Montserrat-SemiBold.woff2") format("woff2"), url("./assets/fonts/subset-Montserrat-SemiBold.woff") format("woff"), url("./assets/fonts/subset-Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}



